import * as React from "react";
import { createCss } from "../../utils/css";
import "./index.css";

interface Props {
  img?: string;
  name?: string;
  desc?: string;
}

const css = createCss("dd-ui-line");

const Line: React.FC<Props> = props => {
  return (
    <div className={css()}>
      <img className={css("poster")} src={props.img} alt="" />
      <div className={css("content")}>
        <div className={css("name")}>{props.name}</div>
        <div className={css("desc")}>{props.desc}</div>
      </div>
    </div>
  );
};

export default Line;
