import * as React from 'react';
import './index.css';

interface Props {
  img?: string;
  name?: string;
  desc?: string;
}

const css = (name: string) => `dd-ui-banner-${name}`;

const Banner: React.FC<Props> = props => {
  return <img className={css('poster')} src={props.img} alt="" />;
};

export default Banner;
